<template lang="html">
  <div class="recommend" ref="recommend">
    <scroller class="recommend-content" :pulldownRefresh="refreshData" ref="scroller" :data="discList">
      <div class="slider-wrapper">
        <carousel :list="recommends">
          <template scope="data">
            <a :href="data.item.linkUrl" class="item">
              <img @load="imgLoad" :src="data.item.picUrl">
            </a>
          </template>
        </carousel>
      </div>
      <div class="recommend-list">
        <h1 class="list-title">热门歌单推荐</h1>
        <disc-list :discs="discList" @selectItem="selectDisc"></disc-list>
      </div>
    </scroller>
    <router-view></router-view>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import * as atypes from '@/store/action-types'
  import { getRecommend } from 'api/recommend'
  import { miniPlayerHeight } from 'assets/js/config'
  import { ERR_OK } from 'api/config'
  import { catchErrors } from 'api/handler'
  import PlayerMixin from '@/mixins/player-mixin'
  import Carousel from 'components/base/carousel/carousel'
  import Scroller from 'components/base/scroller/scroller'
  import DiscList from 'components/base/disc-list/disc-list'

  export default {
    name: 'recommend',
    components: { Carousel, Scroller, DiscList },
    mixins: [PlayerMixin],
    data () {
      return {
        recommends: []
      }
    },
    created () {
      this._getRecommend()
      this._getDiscList()
    },
    computed: {
      ...mapGetters({
        discList: 'recommendDiscList'
      })
    },
    methods: {
      ...mapActions({
        _getDiscList: atypes.GET_RECOMMEND_DISC_LIST
      }),
      selectDisc (disc) {
        this.$router.push(`/recommend/${disc.id}`)
      },
      // 获取推荐carousel信息
      _getRecommend: catchErrors(async function () {
        const recommendRes = await getRecommend()
        if (recommendRes && recommendRes.code === ERR_OK) {
          this.recommends = recommendRes.data.slider || []
        }
      }),
      // carousel图片加载后调用，只调用一次，用以计算scroller高度
      imgLoad () {
        if (!this.imgLoaded) {
          this.$refs.scroller.refresh()
          this.imgLoaded = true
        }
      },
      // 下拉刷新
      refreshData: async function (done) {
        await Promise.all([ this._getRecommend(), this._getDiscList() ])
        // setTimeout(done, 100)
        done()
      },
      handlePlaylist (playlist) {
        const bottom = playlist.length > 0 ? miniPlayerHeight : 0
        this.$refs.recommend.style.bottom = bottom
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";

  .recommend {
    position: absolute;
    top: 88px;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    .recommend-content {
      display: flex;
      flex-direction: column;
      height: 100%;
      .slider-wrapper {
        position: relative;
        width: 100%;
        overflow: hidden;
        img, a {
          display: block;
          width: 100%;
          text-decoration: none;
          overflow: hidden;
        }
      }
      .recommend-list {
        display: flex;
        flex-direction: column;
        .list-title {
          height: 65px;
          line-height: 65px;
          text-align: center;
          font-size: @font-size-medium;
          color: @color-theme;
        }
      }
    }
  }

</style>



